urule.ComparisonOperator=function(menuCallFun){
	this.inputType=null;
	this.operator="";
	this.container=generateContainer();
	URule.setDomContent(this.container,"");
	this.container.css({
		"font-size":"13px",
		"color":"red",
		"fontWeight":"bold",
		"margin-right":"3px"
	});
	var self=this;
	var onClick=function(menu){
		self.setOperator(menu.name);
	};
	self.menu=new URule.menu.Menu({
		onHide:function(){
			menuCallFun();
		},
		menuItems:[{
			label : "",
			name:"GreaterThen",
			onClick : onClick
		}, {
			label : "",
			name:"GreaterThenEquals",
			onClick : onClick
		}, {
			label : "",
			name:"LessThen",
			onClick : onClick
		}, {
			label : "",
			name:"LessThenEquals",
			onClick : onClick
		}, {
			label : "",
			name:"Equals",
			onClick : onClick
		},  {
			label : "()",
			name:"EqualsIgnoreCase",
			onClick : onClick
		},  {
			label : "",
			name:"StartWith",
			onClick : onClick
		}, {
			label : "",
			name:"NotStartWith",
			onClick : onClick
		},{
			label : "",
			name:"EndWith",
			onClick : onClick
		},{
			label : "",
			name:"NotEndWith",
			onClick : onClick
		}, {
			label : "",
			name:"NotEquals",
			onClick : onClick
		}, {
			label : "()",
			name:"NotEqualsIgnoreCase",
			onClick : onClick
		}, {
			label : "",
			name:"In",
			onClick : onClick
		}, {
			label : "",
			name:"NotIn",
			onClick : onClick
		}, {
			label : "",
			name:"Null",
			onClick : onClick
		}, {
			label : "",
			name:"NotNull",
			onClick : onClick
		}, {
			label : "",
			name:"Match",
			onClick : onClick
		}, {
			label : "",
			name:"NotMatch",
			onClick : onClick
		}, {
			label : "",
			name:"Contain",
			onClick : onClick
		}, {
			label : "",
			name:"NotContain",
			onClick : onClick
		}]
	});
	this.container.click(function(e){
		self.menu.show(e);
	});
	
};
urule.ComparisonOperator.prototype.initRightValue=function(data){
	if(!this.inputType){
		return;
	}
	this.inputType.setValueType(data["valueType"],data);
};

urule.ComparisonOperator.prototype.setOperator=function(operator){
	switch(operator){
	case "GreaterThen":
		this.operator="GreaterThen";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();	
		break;
	case "GreaterThenEquals":
		this.operator="GreaterThenEquals";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();	
		break;
	case "LessThen":
		this.operator="LessThen";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();	
		break;
	case "LessThenEquals":
		this.operator="LessThenEquals";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();	
		break;
	case "StartWith":
		this.operator="StartWith";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();	
		break;
	case "Equals":
		this.operator="Equals";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();	
		break;
	case "EqualsIgnoreCase":
		this.operator="EqualsIgnoreCase";
		URule.setDomContent(this.container,"()");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();	
		break;
	case "NotStartWith":
		this.operator="NotStartWith";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();	
		break;
	case "EndWith":
		this.operator="EndWith";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();	
		break;
	case "NotEndWith":
		this.operator="NotEndWith";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();
		break;
	case "NotEquals":
		this.operator="NotEquals";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();
		break;
	case "NotEqualsIgnoreCase":
		this.operator="NotEqualsIgnoreCase";
		URule.setDomContent(this.container,"()");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();
		break;
	case "In":
		this.operator="In";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType("");		
		break;
	case "NotIn":
		this.operator="NotIn";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType("");		
		break;
	case "Null":
		this.operator="Null";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
			this.inputType=null;
		}
		break;
	case "NotNull":
		this.operator="NotNull";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
			this.inputType=null;
		}
		break;
	case "Match":
		this.operator="Match";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();
		break;
	case "NotMatch":
		this.operator="NotMatch";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();
		break;
	case "Contain":
		this.operator="Contain";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();
		break;
	case "NotContain":
		this.operator="NotContain";
		URule.setDomContent(this.container,"");
		if(this.inputType){
			this.inputType.getContainer().remove();
		}
		this.inputType=new urule.InputType();
		break;
	}
	window._setDirty();
};
urule.ComparisonOperator.prototype.getOperator=function(){
	if(this.operator==""){
		throw "";
	}
	return this.operator;
};
urule.ComparisonOperator.prototype.getInputType=function(){
	return this.inputType;
};
urule.ComparisonOperator.prototype.getContainer=function(){
	return this.container;
};